import { useParams } from "react-router";
import { Link } from "react-router-dom";

export default function ProdEdit() {
  const { id } = useParams();

  return (
    <div className="prodEdit">
      <label for="prodname">Name</label>
      <input type="text" className="form-control mb-3" id="prodname"></input>

      <label for="description">Description</label>
      <input type="text" className="form-control mb-3" id="description"></input>

      <label for="price">Price (in Rs.)</label>
      <input type="number" className="form-control mb-3" id="price"></input>
      <div className="m-auto">
        <Link to="/prodList">
          <button className="btn btn-primary mr-3">Save</button>
        </Link>
        <Link to="/prodlist">
          <button className="btn btn-primary">Cancel</button>
        </Link>
      </div>
    </div>
  );
}
