import { useContext, useState } from "react";
import { Link } from "react-router-dom";

import UserContext from "./userContext";

export default function ProdCreate() {
  let data = useContext(UserContext);

  let [prodName, setprodName] = useState("");
  let [price, setprice] = useState("");
  let [stocks, setstocks] = useState("");
  let [sold, setsold] = useState("");

  let handleSubmit = (e) => {
    e.preventDefault();
    data.setproducts([
      ...data.products,
      {
        prodName,
        price,
        stocks,
        sold,
      },
    ]);
    console.log({
      prodName,
      price,
      stocks,
      sold,
    });
    let c = alert("Product Saved Successfully");
    if (c) {
      window.location = "http://localhost:3000/prodlist";
      // <Link to="/prodlist"></Link>;
    }
  };

  return (
    <div className="container prodCreate">
      <form onSubmit={handleSubmit}>
        <div className="row">
          <div className="col-lg-6">
            <label for="prodname">Name</label>
            <input
              type="text"
              className="form-control mb-3"
              id="prodname"
              value={prodName}
              onChange={(e) => setprodName(e.target.value)}
              required
            ></input>
          </div>
          <div className="col-lg-6">
            <label for="price">Price (in Rs.)</label>
            <input
              type="number"
              className="form-control mb-3"
              id="price"
              onChange={(e) => setprice(e.target.value)}
              required
            ></input>
          </div>
          <div className="col-lg-6">
            <label for="stocks">Stocks in Hand</label>
            <input
              type="number"
              className="form-control mb-3"
              id="stocks"
              onChange={(e) => setstocks(e.target.value)}
              required
            ></input>
          </div>
          <div className="col-lg-6">
            <label for="sold">Sold Quantity</label>
            <input
              type="number"
              className="form-control mb-3"
              id="sold"
              onChange={(e) => setsold(e.target.value)}
              required
            ></input>
          </div>
          <div className="col-lg-6">
            <button type="submit" className="btn btn-primary">
              Submit
            </button>
          </div>
        </div>
      </form>
    </div>
  );
}
